import React, { Fragment } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { logout } from '../actions/auth';
import Alert from './Alert';
import PropTypes from 'prop-types';

import logo from '../assets/images/logo.png';


const Navbar = ({ auth: { isAuthenticated, loading }, logout }) => {
	const authLinks = (
		<a className="navbar__top__auth__link" onClick={logout} href="/">LogOut</a>
	);

	const guestLinks = (
		<Fragment>
			<Link className="navbar__top__auth__link" to='/login'>LoGin</Link>
			<Link className="navbar__top__auth__link" to='/signup'>SignUp</Link>
		</Fragment>
	);
	return (
		<Fragment>
			<nav className='navbar'>
				<div className='navbar__top'>
					<div className='navbar__top__logo'>
						<Link className='navbar__top__logo__link' to='/'><img src={logo} alt="logo" /> DallaL Dai</Link>
					</div>
					<div className="navbar__top__auth">
						{!loading && (<Fragment>{isAuthenticated ? authLinks : guestLinks}</Fragment>)}
					</div>
				</div>
				<div className='navbar__bottom'>
					<NavLink exact className='navbar__bottom__item' activeClassName="active" to='/'>Home</NavLink>
					<NavLink className='navbar__bottom__item' activeClassName="active" to='/listings'>Listings</NavLink>
					<NavLink className='navbar__bottom__item' activeClassName="active" to='/about'>About</NavLink>
					<NavLink className='navbar__bottom__item' activeClassName="active" to='/contact'>Contact</NavLink>
				</div>
			</nav>
			<Alert />
		</Fragment>
	)
}

Navbar.propTypes = {
	logout: PropTypes.func.isRequired,
	auth: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
	auth: state.auth
})

export default connect(mapStateToProps, { logout })(Navbar);