import React, { useState, useEffect, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import axios from 'axios';
import House from '../assets/images/house.jpg';

const About = () => {

	const [topSeller, setTopSeller] = useState([]);
	const [realtors, setRealtors] = useState([]);

	useEffect(() => {
		const config = {
			headers: {
				"Content-Type": "application/json"
			}
		};

		const getTopSeller = async () => {
			try {
				const res = await axios.get(`http://localhost:8000/realtors/topseller/`, config);
				setTopSeller(res.data);
			}
			catch (err) {

			}
		}
		getTopSeller();
	}, []);

	useEffect(() => {
		const config = {
			headers: {
				"Content-Type": "application/json"
			}
		};
		const getRealtors = async () => {
			try {
				const res = await axios.get(`http://localhost:8000/realtors/`, config);
				setRealtors(res.data);
			}
			catch (err) {

			}
		}

		getRealtors();

	}, []);

	const getAllRealtors = () => {
		let allRealtors = [];
		let results = [];

		realtors.map(realtor => {
			return allRealtors.push(
				<Fragment key={realtor.id}>
					<div className='about__display'>
						<img className="about__display__image" src={realtor.photo} alt='' />
					</div>
					<h3 className='about__realtor'>{realtor.name}</h3>
					<p className='about__contact'>{realtor.phone}</p>
					<p className='about__contact'>{realtor.email}</p>
					<p className='about__about'>{realtor.description}</p>
				</Fragment>
			);
		});

		for (let i = 0; i < realtors.length; i += 3) {
			results.push(
				<div key={i} className='row'>
					<div className='col-1-of-3'>
						{allRealtors[i]}
					</div>
					<div className='col-1-of-3'>
						{allRealtors[i + 1] ? allRealtors[i + 1] : null}
					</div>
					<div className='col-1-of-3'>
						{allRealtors[i + 2] ? allRealtors[i + 2] : null}
					</div>
				</div>
			);
		}

		return results;
	};

	const getTopSeller = () => {
		let result = [];

		topSeller.map(seller => {
			return result.push(
				<Fragment key={seller.id}>
					<div className='about__display'>
						<img className="about__display__image" src={seller.photo} alt='' />
					</div>
					<h3 className='about__topseller'>Top Seller: </h3>
					<p className='about__realtor'>{seller.name}</p>
					<p className='about__contact'>{seller.phone}</p>
					<p className='about__contact'>{seller.email}</p>
					<p className='about__about'>{seller.description}</p>
				</Fragment>
			);
		});

		return result;
	}

	return (
		<main className='about'>
			<Helmet>
				<title>DallaL Dai - About</title>
				<meta name='description' content='About us' />
			</Helmet>
			<header className='about__header'>
				<h1 className='about__heading'>About DallaL Dai</h1>
			</header>
			<section className="about__info">
				<div className='row'>
					<div className='col-3-of-4'>
						<h2 className='about__subheading'>We find the perfect Home for you</h2>
						<p className="about__paragraph">You pay realtors because they have reach and proven results, and the Noble Black Team wants you to know it’s extraordinary at both. It helps that the results-driven sentiment here drives home the abstract idea of “extraordinary reach”.
							Real estate is a crowded field. But with the number of buyers purchasing homes through real estate agents and brokers growing 18% since 2001, there’s plenty of work for hungry realtors everywhere.
							What matters is your brokerage or personal brand sets itself apart from the rest of the noise. How do you do that? A great tagline or slogan is the perfect place to start.
						</p>
						<div className="about__display">
							<img className='about__display__image' src={House} alt='' />
						</div>
						<p className='about__paragraph'>
							Whether buying or renting, finding the right place to live is a personal and unique experience. This trendy company helps New Yorkers search apartments, find roommates, apply, and pay. The idea of finding a nook of your own is one that many of us can relate to, and Nooklyn is smart to use it to their advantage.
						</p>
					</div>
					<div className='col-1-of-4'>
						{getTopSeller()}
					</div>
				</div>
			</section>
			<section className='about__team'>
				<div className='row'>
					<h2 className='about__subheading'>Meet Our Team!</h2>
				</div>
				{getAllRealtors()}
			</section>
		</main>
	)

}
export default About;